.card q::before {
    content: '\201c';
    color: rgb(255, 119, 0);
    font-size: 2em;
    font-style: normal;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

.carousel {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.carousel-container {
    display: flex;
    object-fit: cover;
    align-items: center;
    justify-content: center;
}

.carousel-heading{
    margin-bottom: 3vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.carousel-title {
    letter-spacing: 1px;
    margin-bottom: 0;
}

.carousel-subtitle {
    margin-top: 1px;
    letter-spacing: 1px;
    font-size: 10px;
    opacity: 0.5;
}

.mySwiper{
    background-color: blue;
    height: 65vh;
    background-color: inherit;
}

